import {StaticImage} from 'gatsby-plugin-image';
import React from 'react';

import Button from './common/Button';
import {FooterLogo, LinkArrow, RoundArrow} from './common/icons';

const breakableEmail = email => email.replace('@', '@\u200B');

const Email = ({
  title, email, className, children,
}) => (
  <div className={className}>
    <p className="text-primary text-lg mb-4 uppercase">{title}</p>
    <a href={`mailto:${email}`} className="text-n-100 text-4.5xl uppercase leading-tight">{children || email}</a>
  </div>
);

const Office = ({
  city, address, phone, email, className,
}) => (
  <div className={`text-n-200 font-mono pr-16 ${className}`}>
    <StaticImage src="../images/clock.png" alt="Clock" className="w-16 mb-8" />
    <p className="font-base text-n-100 text-2xl mb-4 uppercase">{city}</p>
    <p className="mb-7">{address}</p>
    <a href={`tel:${phone}`} className="block">
      P.
      {' '}
      {phone}
    </a>
    <a href={`mailto:${email}`} className="uppercase block">
      E.
      {' '}
      {email}
    </a>
  </div>
);

export default () => (
  <footer className="relative">
    <StaticImage src="../images/footer-background.jpg" alt="" className="absolute inset-0" style={{position: 'absolute'}} />
    <div className="relative pt-56 pb-12 bg-noise">
      <div className="container grid grid-cols-8 gap-y-36 ">
        <div className="col-span-2 flex items-center">
          <FooterLogo className="text-primary text-8xl" />
        </div>
        <Office className="col-span-2 col-start-5" city="New York" address="2708 Meadow View Drive, New York, 94940" phone="+1 860-532-6069" email={breakableEmail('nyc@elitealliance.net')} />
        <Office className="col-span-2" city="Zagreb" address="Tkalčićeva, HR 10000 Ul. Kneza Borne 2, Zagreb" phone="+385 1 4553 535" email={breakableEmail('hq@elitealliance.net')} />
        <div className="col-span-2 flex flex-col justify-end items-start">
          <p className="text-3xl text-n-100 uppercase mb-16 leading-normal">
            We pride ourselves in helping Creators like you.
          </p>
          <Button kind="primary" icon={<RoundArrow className="transform -rotate-90" />}>
            Schedule an appointment
          </Button>
        </div>
        <div className="col-start-5 col-span-4">
          <Email
            title="General"
            email="hey@elitealliance.net"
            className="mb-16"
          >
            {breakableEmail('hey@elitealliance.net')}
            <LinkArrow className="ml-4 mb-2 text-4xl" />
          </Email>
          <Email
            title="For Brands"
            email={breakableEmail('michael@elitealliance.net')}
          />
        </div>
        <p className="col-start-5 col-span-3 font-mono text-xs uppercase text-n-400 opacity-70">
          ©
          {new Date().getFullYear()}
          {' '}
          Elite Alliance. All Rights Reserved.
        </p>
      </div>
    </div>
  </footer>
);
