import {StaticImage} from 'gatsby-plugin-image';
import React, {useState} from 'react';

import Button from './common/Button';
import {RoundArrow} from './common/icons';

const Spoiler = ({title, children}) => {
  const [opened, setOpened] = useState(false);

  return (
    <div className={`py-12 px-10 border-t ${opened ? 'border-n-500 border-opacity-70' : 'border-n-400'}`}>
      <button type="button" className="w-full flex" onClick={() => setOpened(!opened)}>
        <h3 className="text-left text-2xl uppercase pr-40 flex-1">{title}</h3>
        <div className="w-4 h-4 mt-2 flex items-center justify-center">
          <StaticImage src="../images/minus.png" alt="Close" className="w-3" style={{display: !opened ? 'none' : 'block'}} />
          <StaticImage src="../images/plus.png" alt="Open" className="w-4" style={{display: opened ? 'none' : 'block'}} />
        </div>
      </button>
      <div className={`overflow-hidden grid grid-cols-2 gap-24 pr-10 font-mono text-justify transition-all ${!opened ? 'h-0 opacity-0' : 'pt-14 opacity-100'}`}>
        {children}
      </div>
    </div>
  );
};

